import React, { useState } from "react";
import NavLeft from "./NavLeft";

function HeaderExpand() {
  const [isClicked, setIsClicked] = useState<boolean>(true);

  return (
    <div className="expandheader">
      <p
        className="imgExpand"
        onClick={() => {
          setIsClicked(!isClicked);
        }}
      ></p>

      {!isClicked ? <NavLeft /> : <div></div>}
      <div
        className={!isClicked ? "overlay" : "overlay hidden"}
        onClick={() => {
          setIsClicked(!isClicked);
        }}
      ></div>
    </div>
  );
}

export default HeaderExpand;
